/**
* The following styles get applied inside the editor only.
*
* Replace them with your own styles or remove the file completely.
*/
.wp-block-lf-image-box {
  padding: 0.5rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr)); }
  .wp-block-lf-image-box > div {
    border: 1px solid lightgray;
    border-radius: 25px; }
  .wp-block-lf-image-box > div:not(:last-child) {
    margin-right: 0.75rem; }
  .wp-block-lf-image-box.align-center p,
  .wp-block-lf-image-box.align-center h4 {
    text-align: center; }
  .wp-block-lf-image-box.align-left p,
  .wp-block-lf-image-box.align-left h4 {
    text-align: left; }
  .wp-block-lf-image-box.align-right p,
  .wp-block-lf-image-box.align-right h4 {
    text-align: right; }
  .wp-block-lf-image-box.columns-1 .column:not(:nth-child(1)) {
    display: none; }
  .wp-block-lf-image-box.columns-2 .column:nth-child(3),
  .wp-block-lf-image-box.columns-2 .column:nth-child(4) {
    display: none; }
  .wp-block-lf-image-box.columns-3 .column:nth-child(4) {
    display: none; }
  .wp-block-lf-image-box .column-image {
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    height: var(--image-box-height);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: rgba(139, 139, 150, 0.1); }
  .wp-block-lf-image-box .block-editor-media-placeholder__button {
    margin-left: auto;
    margin-right: auto;
    display: block; }
  .wp-block-lf-image-box .column-1 .column-image {
    background-image: var(--image-box-img1); }
  .wp-block-lf-image-box .column-2 .column-image {
    background-image: var(--image-box-img2); }
  .wp-block-lf-image-box .column-3 .column-image {
    background-image: var(--image-box-img3); }
  .wp-block-lf-image-box .column-4 .column-image {
    background-image: var(--image-box-img4); }
  .wp-block-lf-image-box .block-editor-url-input {
    max-width: 100%; }
    .wp-block-lf-image-box .block-editor-url-input input {
      max-width: 100%; }
  .wp-block-lf-image-box .column-text {
    padding: 0.75rem 0.5rem; }
  .wp-block-lf-image-box .components-placeholder.components-placeholder {
    background-color: transparent; }
  .wp-block-lf-image-box .blocks-button__inline-link {
    display: flex;
    max-width: 100%;
    align-items: center;
    min-height: 20px; }
    .wp-block-lf-image-box .blocks-button__inline-link .block-editor-block-list__block .block-editor-url-input input[type=text],
    .wp-block-lf-image-box .blocks-button__inline-link .block-editor-url-input input[type=text],
    .wp-block-lf-image-box .blocks-button__inline-link .components-popover .block-editor-url-input input[type=text] {
      width: 100%;
      border: 1px;
      padding: 6px 8px;
      box-shadow: 0 0 0 transparent;
      transition: box-shadow .1s linear;
      border-radius: 4px;
      border: 1px solid #7e8993;
      line-height: normal; }
